import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import addForm from './add-form.vue';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.className = '店员管理';
        this.businessUserList = [];
        this.showEditbox = false;
    }
    get shopId() {
        return this.$route.query.shopId;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    mounted() {
        this.getBusinessUserList();
    }
    addUser() {
        if (this.businessUserList.length >= 30) {
            this.$createDialog({
                type: 'confirm',
                title: '最多添加30个店员',
                onConfirm: (e) => { }
            }).show();
            return;
        }
        this.showEditbox = true;
    }
    getBusinessUserList() {
        this.$store.dispatch('getBusinessUserList', { shopId: this.shopId }).then((res) => {
            if (res.success) {
                this.businessUserList = res.data;
            }
        });
    }
    testSend(obj) {
        if (!obj.qwUserId) {
            this.$createDialog({
                type: 'confirm',
                title: '店员绑定异常，请联系管理员处理',
                onConfirm: (e) => { }
            }).show();
            return;
        }
        this.$store.dispatch('sendTestMessage', { qwUserId: obj.qwUserId }).then((res) => {
            if (res.success) {
                this.$createDialog({
                    type: 'confirm',
                    title: '消息已推送，未收到请联系管理员。',
                    onConfirm: (e) => { }
                }).show();
            }
        });
    }
    addSuccess() {
        this.showEditbox = false;
        this.$createDialog({
            type: 'confirm',
            title: '添加成功',
            onConfirm: (e) => { }
        }).show();
        this.getBusinessUserList();
    }
    deleteUser(obj) {
        // deleteUserByPhoneOrId
        this.$store.dispatch('deleteUserByPhoneOrId', { qwUserId: obj.qwUserId || '', phone: obj.bindPhone || '', shopId: this.shopId }).then((res) => {
            if (res.success) {
                this.$createDialog({
                    type: 'confirm',
                    title: '删除成功',
                    onConfirm: (e) => { }
                }).show();
                this.getBusinessUserList();
            }
            else {
                this.$createDialog({
                    type: 'confirm',
                    title: res.message,
                    onConfirm: (e) => { }
                }).show();
            }
        });
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox,
            addForm
        }
    })
], businessManagement);
export default businessManagement;
