import { Component, Vue, Prop } from 'vue-property-decorator';
let AddForm = class AddForm extends Vue {
    constructor() {
        super(...arguments);
        // ----------------内部属性-----------------------
        this.validity = {};
        this.valid = undefined;
        this.model = {
            name: this.shopName + '-店员' + parseInt(Math.random() * 10, 10),
            phone: '',
            shopId: this.shopId
        };
        this.schema = {
            groups: [
                {
                    legend: '店员添加',
                    fields: [
                        {
                            type: 'input',
                            modelKey: 'name',
                            label: '名称',
                            props: {
                                placeholder: '请输入'
                            },
                            rules: {
                                required: true
                            },
                            // validating when blur
                            trigger: 'blur',
                            messages: { required: '店员名称必填' },
                        },
                        {
                            type: 'input',
                            modelKey: 'phone',
                            label: '手机号',
                            props: {
                                placeholder: '请输入'
                            },
                            rules: {
                                required: true,
                                type: 'tel'
                            },
                            messages: { required: '手机号必填' },
                            // validating when blur
                            trigger: 'blur'
                        }
                    ]
                },
                {
                    fields: [
                        {
                            type: 'submit',
                            label: '保存'
                        },
                        {
                            type: 'reset',
                            label: '取消'
                        }
                    ]
                }
            ]
        };
        this.options = {
            scrollToInvalidField: true,
            layout: 'classic' // classic fresh
        };
        // ----------------生命周期-----------------------
    }
    // ----------------计算属性-----------------------
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
